// source: https://www.canva.com/learn/website-color-schemes/
$paper: #F4F4F4;
$charcoal: #373737;

$headerFontColor: $paper;
$sidebarBg: $paper;
$fontColor: $charcoal;

$primaryGreen: #196A41;
$secondaryGreen: #489370;
$primaryGray: #A5A5A5;

$headerHeight: 6.5rem;
$footerHeight: 2rem;
$bodyHeight: calc(100vh - #{$headerHeight} - #{$footerHeight});

$smallerFontSoze: 0.9rem;
$standardFontSize: 1rem;
$biggerFontSize: 1.2rem;

.app {
  color: $fontColor;

  .icon-button {
    cursor: pointer;
  }

  .loading-container {
    height: $bodyHeight;
    flex-direction: column;
    display: flex;
    justify-content: center;

    .loading {
      padding: 1rem;
      font-size: $biggerFontSize;
      text-align: center;
      color: $primaryGreen;
    }
  }

  .no-link {
    &:link {
      text-decoration: inherit;
      color: inherit;
    }

    &:visited {
      text-decoration: inherit;
      color: inherit;
    }
  }

  p, h1, h2, h3, h4, h5, h6 {
    margin: 0;
  }

  h1 {
    font-size: $biggerFontSize;
    font-weight: normal;
  }

  h2 {
    font-size: $biggerFontSize;
    font-weight: normal;
    width: fit-content;
    max-width: 100%;
    text-overflow: ellipsis;
    white-space: nowrap;
    overflow: hidden;
  }

  h3 {
    font-size: $biggerFontSize;
    font-weight: normal;
  }

  .header {
    color: lighten($paper, 30%);
    background: $secondaryGreen;
    padding: 1rem;

    a {
      &:link {
        color: inherit;
      }

      &:visited {
        color: inherit;
      }
    }
  }

.active-article {
    background: rgba(72,147,112,0.3);
}

.active-group {
    background: rgba(72,147,112,0.3);

}

  .list-entry {
    cursor: pointer;
    border-bottom: solid 1px lighten($fontColor, 40%);
    display: flex;



    .list-entry-text {
      display: block;
      padding: 0.2rem;
      padding-left: 1.0rem;
      text-overflow: ellipsis;
      overflow: hidden;
    }

    .title {
      font-size: $standardFontSize;
      font-weight: 400;
    }

    .subtitle {
      font-size: $smallerFontSoze;
    }

    .bold {
      font-weight: bold;
    }

    .float {
      font-size: 1.3rem;
      color: $primaryGreen;
      margin: 1.55rem 0 1.55rem 1rem;
    }
  }




  .thread-detail {

    .article-detail {
      // show \n as breaks: https://stackoverflow.com/a/44413780/2306363
      white-space: pre-line;

      .article-detail-author {
        font-size: $smallerFontSoze;
        text-overflow: ellipsis;
        overflow-wrap: break-word;
      }

      .article-detail-chevron {
        margin-right: 0.5rem;
      }

      .article-detail-content {
        padding: 1rem;
        font-size: $standardFontSize;

        .nested-content {
          margin-left: 1rem;
          border-left: solid #99A3CC 0.15rem;
          padding-left: 0.5rem;
        }
      }

      .article-buttons {
        display: flex;
        flex-grow: 1;
        justify-content: flex-end;
        .article-button {
          margin-left: 1rem;
        }
      }

      .header {
        display: flex;
        align-items: center;
      }
    }

    .collapsible-thread-list {
      .header {
        padding: 0.5rem;
        cursor: pointer;
      }

      .article-detail-title {
        font-size: $biggerFontSize;
      }

      .collapsible-line {
        position: absolute;
        width: 0.25rem;
        cursor: pointer;
        top: 0;
        bottom: 0;
        background: $secondaryGreen;
      }
    }

    li {
      position: relative;
    }

    ul {
      list-style-type: none;
      padding-left: 0.5rem;
    }
  }

  .no-thread {
    height: $bodyHeight;
    flex-direction: column;
    display: flex;
    justify-content: center;
  }

  .no-thread-text {
    font-size: $biggerFontSize;
    text-align: center;
    color: $primaryGreen
  }
