  /*Sidebar-Thunderbirdstyle*/
  .sidebar-content {
    height: $bodyHeight;
    display: grid;
    //grid-template-rows: repeat(auto-fit, auto);
    //grid-template-columns: repeat(auto-fit, auto);
    grid-template-columns: 0.5fr 0.7fr 1fr;
    grid-template-rows: 1.6fr 2.4fr;
    gap: 5px 5px;
    grid-template-areas:
      "Sidebar Content-1 Content-1"
      "Sidebar Content-2 Content-2";
  }
  .sidebar-content-sidebar { grid-area: Sidebar; background: $sidebarBg; overflow-y: scroll; border-right: solid 1px lighten($fontColor, 50%);}
  .sidebar-content-content-1 { grid-area: Content-1; background: $sidebarBg; overflow-y: scroll;}
  .sidebar-content-content-2 { grid-area: Content-2; overflow-y: auto;}


  .app-grid {

    .app-grid-header {
      position: fixed;
      top: 0;
      left: 0;
      right: 0;
      height: $headerHeight;
      z-index: 10;
      color: lighten($paper, 30%);
      background: $primaryGreen;
      padding: 1rem;
      display: flex;

      a {
        &:link {
          color: inherit;
        }

        &:visited {
          color: inherit;
        }
      }

      .search-bar {
        flex-grow: 1;
        overflow: hidden;
      }

      img {
        width: 3rem;
        margin-top: 0.8rem;
        margin-bottom: 0.8rem;
      }

      .heading-subtitle {
        margin-top: 1rem;
      }



      .logo {
        margin-right: 0.8rem;
        width: available;
      }

      .icon {
        font-size: $biggerFontSize;
        padding-top: 1.65rem;
        padding-right: 0.8rem;
        padding-left: 0.8rem;
        padding-bottom: 1.65rem;
        float: right;
      }
    }

    .app-grid-body {
      margin-top: $headerHeight;
      margin-bottom: $footerHeight;
      overflow-x: hidden;
      min-width: 0;
    }

    .app-grid-footer {
      display: flex;
      height: $footerHeight;
      position: fixed;
      bottom: 0;
      left: 0;
      right: 0;
      z-index: 10;
      color: lighten($paper, 30%);
      background: $primaryGreen;
      padding: 0.5rem;
      .app-name {
        flex-grow: 1;
      }
    }
  }

  .search {
    margin-top: 0.8rem;
    font-size: $standardFontSize;
    width: 100%;
    max-width: 30rem;
  }
}

.attachments {
  img {
    max-width: 100%;
  }
}

form.post-article {
  width: 100%;
  max-width: 40rem;

  .input-group {
    display: flex;
    justify-content: center;
    margin: 1rem;
    @media only screen and (max-width: 47rem) {
      flex-direction: column;
    }
  }

  input, textarea {
    border: 1px solid $primaryGreen;
    font-size: $standardFontSize;
    flex-grow: 2;
    padding: 0.5rem;
    margin-right: 1rem;
    min-width: 0;

    @media only screen and (max-width: 47rem) {
      margin-right: 0;
      margin-bottom: 1rem;
    }

    &:last-child {
      margin-right: 0;
      margin-bottom: 0;
    }
  }

  textarea {
    min-height: 10rem;
    resize: vertical;
    flex-grow: 2;
  }

  button {
    color: white;
    background-color: $primaryGreen;
    padding: 1rem;
    border: 1px solid $primaryGreen;
    font-size: $biggerFontSize;
    display: inline-block;
    flex-grow: 2;
    margin-right: 1rem;
    cursor: pointer;

    @media only screen and (max-width: 47rem) {
      margin-right: 0;
      margin-bottom: 1rem;
    }

    &:disabled {
      background-color: $primaryGray;
    }

    &.back {
      background-color: white;
      color: $fontColor;
    }

    &:last-child {
      margin-right: 0;
      margin-bottom: 0;
    }
  }
}

* {
  box-sizing: border-box;
}
